<template>
    <div>고객관리</div>
    <table>
        <thead>
            <tr>
                <th>id</th>
                <th>Name</th>
                <th>email</th>
                <th>phone</th>
                <th>address</th>
            </tr>
        </thead>
        <tbody>
            <tr v-for="c in customers">
                <td v-text="c.id"></td>
                <td v-text="c.name"></td>
                <td v-text="c.email"></td>
                <td v-text="c.phone"></td>
                <td v-text="c.address"></td>
            </tr>
        </tbody>
    </table>
</template>

<script>
import axios from "axios";
export default { 
    data() { 
        return {
            customers: []
        }
    },
    created() {
        const url = `/api/customer`;
        axios.get(url)
            .then(json => {
                return this.customers = json.data;
            })
    },
    methods: {
        
    }
}
</script>

